.main-container {
  width: 100%;
  height: auto;
  background-color: #00aeef;
  color: #fff;
  /* span {
        text-align: center;
        font-weight: bold;
        background-color: #028bbd;
        padding: 15px;
        border-radius: 150px;
    } */ }
  .main-container h1 {
    width: 100%;
    font-size: 35px;
    text-align: center; }
  .main-container .main-banner {
    padding: 130px 0px 40px 0px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    justify-items: center; }
    .main-container .main-banner button {
      margin: 50px 0px 20px 0px;
      float: left;
      padding: 10px 50px; }

.main-features-block .main-features {
  margin: 15px 0px;
  color: #494d49; }
  .main-features-block .main-features .divider {
    display: block; }

.main-features-block .main-features-item {
  margin: 0px 0px 50px 0px; }
  .main-features-block .main-features-item h3 {
    font-size: 20px;
    margin: 0px 5px;
    color: #494d49; }
  .main-features-block .main-features-item ul {
    padding-left: 10px;
    list-style: none; }
    .main-features-block .main-features-item ul li {
      margin: 20px 0px;
      font-weight: bold; }
      .main-features-block .main-features-item ul li i {
        color: green; }

.main-features-block .divider {
  display: none; }

@media (max-width: 991px) {
  .main-container h1 {
    font-size: 25px; }
  .main-container .main-banner {
    padding: 100px 0px; }
  .main-features h1 {
    text-align: center; }
  .main-features-item {
    margin: 15px 0px; }
  .divider {
    display: block !important; } }

@media (max-width: 576px) {
  .main-container h1 {
    font-size: 15px; }
  .main-container span {
    font-size: 13px;
    padding: 10px; }
  .main-features h1 {
    text-align: center;
    font-size: 20px; }
  .main-features-item h3 {
    text-align: center;
    font-size: 15px; }
    .main-features-item h3 .badge {
      display: none; } }
