.app-header {
  font-family: 'Open Sans', sans-serif;
  padding: 10px;
  background-color: #fff;
  font-size: 15px;
  font-weight: 600;
  color: #000;
}

.app-header a {
  color: #5e707d;
}

.app-header i {
  font-size: 3rem;
}

.app-header .navbar-toggler {
  border: none;
}

.app-header img {
  width: 64px;
  border-radius: 150px;
  margin: 0px 20px;
}

.app-header .navbar-links .nav-item {
  padding: 1rem;
}

.app-header .navbar-links .nav-item a {
  padding: 5px 0px;
  margin: 0px 5px;
  position: relative;
}

.app-header .navbar-links .nav-item a:after {
  content: '';
  opacity: 0;
  position: absolute;
  bottom: 0;
  background-color: #00aeef;
  width: 100%;
  height: 1px;
  left: 0px;
}

.app-header .navbar-links .nav-item a:hover:after {
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.app-header .nav-item {
  padding: 1rem 1.5rem;
}

.app-header .nav-item .dropdown-toggle {
  margin: 0px 5px;
}

@media (max-width: 991px) {
  .app-header {
    padding: 5px;
  }
  .nav-item {
    padding: 1rem !important;
  }
}
